import { useQuery } from '@tanstack/react-query';
import { helperUrl } from '../usePosts';

const fetchACFOptions = async () => {
  let response = await helperUrl(`/wp-json/acf_options/all`, null);

  return await response?.json();
};

const useACFOptionsData = () => {
  return useQuery({
    queryKey: ['ACFOptions'],
    queryFn: () => fetchACFOptions(),
    keepPreviousData: true,
  });
};

export { useACFOptionsData, fetchACFOptions };
