import React from 'react';
import styles from '../../styles/Home.module.scss';
import clsx from 'clsx';
import LinkButton from '../../components/LinkButton/link-button.component';
import Layout from '../../components/Layout/layout';
import RadioButton from '../../components/RadioButton/radio-button-component';
import Checkbox from '../../components/Checkbox/checkbox-component';
import { useForm } from 'react-hook-form';
import { urlProd } from '../../constants';
import { useEffect, useState } from 'react';
import { usePosts } from '../../hooks/usePosts';
import { usePageData } from '../../hooks/usePage';
import { useMutation } from '@tanstack/react-query';
import { sendForm } from '../../api';
import { useACFOptionsData } from '../../hooks/useACFOptions';

function AppLayout() {
  const [page, setPage] = useState(1);
  const [pluginDirUrl, setPluginDirUrl] = useState('');
  const [link_in_bio_posts, setlink_in_bio_posts] = useState<any[]>([]);
  const [sendFormSuccess, setSendFormSuccess] = useState<string>('');

  const { data: ACFOptionsData, isFetching: isFetchingACFOptionsData } =
    useACFOptionsData();
  const { data: postData, isFetching } = usePosts(page);
  const { data: pageData, isFetching: isFetchingPageData } = usePageData();

  const { mutate: mutateSendForm, isLoading: mutateIsLoading } = useMutation(
    sendForm,
    {
      onSuccess: (data) => {
        setSendFormSuccess(data.message);
      },
      onError: () => {
        alert('Error when sending the form!');
      },
    }
  );

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const onSubmit = (data: any) => {
    const formData = new FormData();
    formData.append('your-name', data.FullName);
    formData.append('your-email', data.YourEmail);
    formData.append('occupation', data.Occupation);
    mutateSendForm({
      id: ACFOptionsData.link_in_bio_contact_form_id,
      formData: formData,
    });
  };

  const helperUrl = async (url: string) => {
    const response = await fetch(
      `${url}/wp-json/rack-a-tier/v2/plugin-dir-url`
    );
    const data = await response.json();

    process.env.REACT_APP_ENV === 'prod' && setPluginDirUrl(data);
  };

  useEffect(() => {
    async function fetchUrlImages() {
      if (
        document.URL.split('/').slice(0, -2).join('/') ===
        window.location.origin
      ) {
        await helperUrl(window.location.origin);
      } else if (document.URL.split('/').slice(0, -2).join('/') === 'https:/') {
        await helperUrl(window.location.origin);
      } else {
        if (document.URL.split('/').length === 7) {
          await helperUrl(document.URL.split('/').slice(0, -3).join('/'));
        } else if (document.URL.split('/').length === 6) {
          await helperUrl(document.URL.split('/').slice(0, -2).join('/'));
        }
      }
    }

    fetchUrlImages();
  }, []);

  useEffect(() => {
    if (postData) {
      setlink_in_bio_posts([...link_in_bio_posts, ...postData.data]);
    }
  }, [postData]);

  return (
    <div className='flex flex-col min-h-[100vh] '>
      <Layout>
        <main className={clsx(styles.main)}>
          <div className='p-[30px_30px_0_30px] max-w-[var(--max-width)] m-auto'>
            <h1
              className={clsx(
                'font-extrabold text-center tracking-[0.02em]',
                styles.mainH1
              )}
            >
              {pageData ? pageData[0].title.rendered : ''}
            </h1>
            {/* Contact Us */}
            <div className='mb-[50px]'>
              <h2
                className={clsx(
                  'mb-2.5 font-extrabold text-center tracking-[0.02em]',
                  styles.mainSocialTitle
                )}
              >
                {ACFOptionsData?.link_in_bio_contact_us_title
                  ? ACFOptionsData?.link_in_bio_contact_us_title
                  : 'Contact Us'}
              </h2>
              <div className='flex flex-wrap gap-[11px] mb-10'>
                {ACFOptionsData?.link_in_bio_repeaterContactUsLinks &&
                  ACFOptionsData?.link_in_bio_repeaterContactUsLinks.map(
                    (item: any, index: number) => {
                      return (
                        <LinkButton
                          key={index}
                          href={
                            item.link_in_bio_repeaterContactUsLinks_URL
                              ? item.link_in_bio_repeaterContactUsLinks_URL
                              : ''
                          }
                          variant='outline'
                          size='big'
                          className='w-full shrink max-w-[calc(50%-11px)] flex-shrink-0'
                        >
                          <img
                            src={
                              item.link_in_bio_repeaterContactUsLinks_Image
                                ? item.link_in_bio_repeaterContactUsLinks_Image
                                    .url
                                : ''
                            }
                            alt={
                              item.link_in_bio_repeaterContactUsLinks_ALT
                                ? item.link_in_bio_repeaterContactUsLinks_ALT
                                : ''
                            }
                          />
                        </LinkButton>
                      );
                    }
                  )}
              </div>
              <h2
                className={clsx(
                  'mb-2.5 font-extrabold text-center tracking-[0.02em]',
                  styles.mainSocialTitle
                )}
              >
                {ACFOptionsData?.link_in_bio_FollowUsTitle
                  ? ACFOptionsData?.link_in_bio_FollowUsTitle
                  : 'Follow Us'}
              </h2>
              <div className='flex flex-wrap justify-between m-auto xsm:gap-[10px] sm:gap-[25px] mb-10'>
                {ACFOptionsData?.link_in_bio_repeaterFollowUsLinks &&
                  ACFOptionsData?.link_in_bio_repeaterFollowUsLinks.map(
                    (item: any, index: number) => {
                      return (
                        <a
                          key={index}
                          href={
                            item.link_in_bio_repeaterFollowUsLinks_URL
                              ? item.link_in_bio_repeaterFollowUsLinks_URL
                              : ''
                          }
                          className='duration-300 ease-in-out rounded-full hover:shadow-[0_4px_16px_3px_rgba(0,0,0,0.22)]'
                        >
                          <img
                            src={
                              item.link_in_bio_repeaterFollowUsLinks_Image.url
                                ? item.link_in_bio_repeaterFollowUsLinks_Image
                                    .url
                                : ''
                            }
                            alt={
                              item.link_in_bio_repeaterFollowUsLinks_ALT
                                ? item.link_in_bio_repeaterFollowUsLinks_ALT
                                : ''
                            }
                          />
                        </a>
                      );
                    }
                  )}
              </div>
            </div>
            {/* Main items */}
            <div className='flex flex-col gap-[25px] mb-[97px] pt-[26px]'>
              {link_in_bio_posts?.map((post: any) => {
                return (
                  <div
                    key={post.id}
                    className='flex gap-[15px] p-[12px_19px_12px_6px] shadow-[0_4px_30px_rgba(0,0,0,0.15)] rounded-[20px]'
                  >
                    <div className='sm:flex-shrink-0 p-[6px] w-[150px] h-[150px] rounded-[10px] border-[0.5px] border-solid border-[#c1c1c1]'>
                      <div className='h-full flex items-center overflow-hidden '>
                        <img
                          className='min-h-full min-w-full max-w-full max-h-full object-contain'
                          src={post.ACF.image.url}
                          alt=''
                        />
                      </div>
                    </div>
                    <div className='flex flex-col justify-center'>
                      <h3 className='line-clamp-2 leading-[24px] text-[19px] sm:leading-[27px] sm:text-[22px] font-semibold'>
                        {post.ACF.title}
                      </h3>
                      {post.ACF.price !== '' &&
                      post.ACF.discount_price === '' ? (
                        <p className='mt-2 text-[var(--text-price)] leading-[19px] text-[16px] font-semibold'>
                          {'$' + post.ACF.price + ' USD'}
                        </p>
                      ) : null}
                      {post.ACF.price !== '' &&
                      post.ACF.discount_price !== '' ? (
                        <>
                          <p className='mt-2 text-[var(--text-price)] leading-[19px] text-[16px] font-semibold'>
                            {'$' + post.ACF.discount_price + ' USD'}
                          </p>
                          <p className='mt-0.5 discount text-[var(--text-price-discount)] line-through leading-[19px] text-[16px] font-semibold'>
                            {'$' + post.ACF.price + ' USD'}
                          </p>
                        </>
                      ) : null}
                      {post.ACF.discount_price !== '' &&
                      post.ACF.price === '' ? (
                        <p className='mt-0.5 discount text-[var(--text-price-discount)] leading-[19px] text-[16px] font-semibold'>
                          {'$' + post.ACF.discount_price + ' USD'}
                        </p>
                      ) : null}
                    </div>
                  </div>
                );
              })}
              {Number(postData?.totalPages) !== page ? (
                <button
                  onClick={() =>
                    Number(postData?.totalPages) !== page && setPage(page + 1)
                  }
                  disabled={isFetching}
                >
                  {isFetching ? 'Loading...' : 'Show More'}
                </button>
              ) : null}
            </div>
          </div>
          {/* Form */}
          <div className='p-[30px_30px_37px_30px] bg-[var(--form-bg)]'>
            <div className='max-w-[var(--max-width)] m-auto'>
              <h2 className='mb-2.5 font-bold text-center leading-[39px] text-[32px]'>
                {ACFOptionsData?.link_in_bio_ContactFormTitle
                  ? ACFOptionsData?.link_in_bio_ContactFormTitle
                  : 'Sign Up to Save'}
              </h2>
              <p className='mb-[18px] font-medium text-center leading-[15px] text-[12px]'>
                {ACFOptionsData?.link_in_bio_ContactFormDescription
                  ? ACFOptionsData?.link_in_bio_ContactFormDescription
                  : 'Join our mailing list to get 20% off select products every week!'}
              </p>
            </div>
            <form
              onSubmit={handleSubmit(onSubmit)}
              className='max-w-[var(--max-width)] m-auto'
            >
              <div className='mb-5 flex flex-col w-full flex-wrap items-stretch'>
                <label
                  className='mb-1.5 tracking-[0.03em] font-medium leading-[17px] text-[14px]'
                  htmlFor='FullName'
                >
                  Your Name*
                </label>
                <input
                  {...register('FullName', {
                    required: 'Full Name is required',
                  })}
                  id='FullName'
                  type='text'
                  className='placeholder:text-[#b3b3b3] relative m-0 block w-full min-w-0 flex-auto rounded-[10px] border border-transparent bg-[#ffffff] bg-clip-padding px-3 py-[14px] font-medium leading-[17px] text-[14px] outline-none transition ease-in-out focus:border-solid focus:border-neutral-600 focus:border-primary-600 focus:shadow-te-primary focus:outline-none'
                  aria-describedby='basic-addon3'
                  placeholder='Full Name'
                />
                {errors.FullName &&
                  (errors.FullName?.message ? (
                    <p className='mt-2 text-[var(--text-price)] text-[12px] leading-[13px] font-semibold'>
                      {errors?.FullName?.message as string}
                    </p>
                  ) : null)}
              </div>
              <div className='mb-[15px] flex flex-col w-full flex-wrap items-stretch'>
                <label
                  className='mb-1.5 tracking-[0.03em] font-medium leading-[17px] text-[14px]'
                  htmlFor='YourEmail'
                >
                  Your Email*
                </label>
                <input
                  {...register('YourEmail', {
                    required: 'Email is required',
                    pattern: {
                      value:
                        /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
                      message: 'invalid email address',
                    },
                  })}
                  id='YourEmail'
                  type='text'
                  className='placeholder:text-[#b3b3b3] relative m-0 block w-full min-w-0 flex-auto rounded-[10px] border border-transparent bg-[#ffffff] bg-clip-padding px-3 py-[14px] font-medium leading-[17px] text-[14px] outline-none transition ease-in-out focus:border-solid focus:border-neutral-600 focus:border-primary-600 focus:shadow-te-primary focus:outline-none'
                  aria-describedby='basic-addon3'
                  placeholder='Email Address'
                />
                {errors.YourEmail &&
                  (errors.YourEmail?.message ? (
                    <p className='mt-2 text-[var(--text-price)] text-[12px] leading-[13px] font-semibold'>
                      {errors?.YourEmail?.message as string}
                    </p>
                  ) : null)}
              </div>
              <div className='mb-[25px] flex items-center justify-center'>
                <p className='mr-5 font-medium leading-[17px] text-[14px] tracking-[0.03em]'>
                  Occupation:
                </p>
                <RadioButton
                  registerField={register('Occupation', {
                    required: 'Occupation is required',
                  })}
                  name='Occupation'
                  id='radioDefault01'
                  label='Wholesaler'
                  value='Wholesaler'
                  additionalClasses='mr-5'
                />
                <RadioButton
                  registerField={register('Occupation', {
                    required: 'Occupation is required',
                  })}
                  name='Occupation'
                  id='radioDefault02'
                  value='Electrician'
                  label='Electrician'
                />
              </div>
              {errors.Occupation &&
                (errors.Occupation?.message ? (
                  <p className='mt-[-20px] mb-3 text-center text-[var(--text-price)] text-[12px] leading-[13px] font-semibold'>
                    {errors?.Occupation?.message as string}
                  </p>
                ) : null)}
              <div className='mb-[18px] border-b-[1px] border-solid border-[#ad8c03] w-full' />
              <Checkbox
                registerField={register('privacy-policy', {
                  required: 'Required',
                })}
                value='privacy-policy'
                label='By Signing up
you agree to Rack-A-Tiers Privacy Policy'
                id='privacy-policy'
                additionalClasses='mb-[30px]'
              />
              {errors['privacy-policy'] &&
                (errors['privacy-policy']?.message ? (
                  <p className='mt-[-30px] mb-3 text-[var(--text-price)] text-[12px] leading-[13px] font-semibold'>
                    {errors['privacy-policy']?.message as string}
                  </p>
                ) : null)}
              {sendFormSuccess !== '' ? (
                <p className='mr-5 font-medium leading-[20px] text-[18px] tracking-[0.03em] text-[rgb(67,195,9)]'>
                  {sendFormSuccess}
                </p>
              ) : null}
              <div className='flex justify-center'>
                <button
                  type='submit'
                  className='min-w-[166px] inline-block rounded-[43.5px] bg-[var(--second)] px-6 pt-[13px] pb-3 text-[16px] font-semibold leading-[19px] text-white transition duration-150 ease-in-out hover:bg-primary-600 hover:shadow-[0_8px_9px_-4px_rgba(59,113,202,0.3),0_4px_18px_0_rgba(59,113,202,0.2)] focus:bg-primary-600 focus:shadow-[0_8px_9px_-4px_rgba(59,113,202,0.3),0_4px_18px_0_rgba(59,113,202,0.2)] focus:outline-none focus:ring-0 active:bg-primary-700 active:shadow-[0_8px_9px_-4px_rgba(59,113,202,0.3),0_4px_18px_0_rgba(59,113,202,0.2)]'
                >
                  Subscribe
                </button>
              </div>
            </form>
          </div>
        </main>
      </Layout>
    </div>
  );
}

export default AppLayout;
