import React from 'react';
import clsx from 'clsx';
import styles from '../../styles/Home.module.scss';
import logo from '../../Logo-whiteslogan.png';
import { useGetFetchQuery } from '../../hooks/useGetFetchQuery';

interface ILayout {
  children: React.ReactNode;
}

export default function Layout({ children }: ILayout) {
  const ACFOptionsData: any = useGetFetchQuery(['ACFOptions']);

  return (
    <>
      <header className={clsx('flex justify-center', styles.AppHeader)}>
        <img
          src={
            ACFOptionsData?.link_in_bio_HeaderLogo
              ? ACFOptionsData?.link_in_bio_HeaderLogo.url
              : logo
          }
          className='App-logo'
          alt='logo'
        />
      </header>
      {children}
      <footer className='pt-[30px] pb-[11px] bg-[var(--second)]'>
        <div className='flex justify-center mb-[40px] relative'>
          <div className='absolute top-[34px] h-[119px] w-full bg-[#141414] z-0 hidden min-[430px]:block' />
          <img
            className='z-20 relative'
            src={
              ACFOptionsData?.link_in_bio_FooterLogo
                ? ACFOptionsData.link_in_bio_FooterLogo.url
                : 'data:image/png;base64,'
            }
            alt='Footer Logo'
          />
        </div>
        <div className='copyright text-[#ffffff] text-center font-medium leading-[18px] text-[10px] lap:leading-[20px] lap:text-[12px]'>
          <p>
            {ACFOptionsData?.link_in_bio_FooterCopyright
              ? ACFOptionsData?.link_in_bio_FooterCopyright
              : '© Copyright 2022 Rack-A-Tiers Mfg. Inc. All Rights Reserved.'}
          </p>
        </div>
      </footer>
    </>
  );
}
