import { useQuery } from '@tanstack/react-query';
import { helperUrl } from '../usePosts';

const fetchPage = async () => {
  let regex = /((\w+)(-)?(\w+))+\/$/;
  let href: any = document.URL;

  let response = await helperUrl(
    `/wp-json/wp/v2/pages?slug=${href.match(regex)[0].replace(/\/$/, '')}`,
    null
  );

  return await response?.json();
};

const usePageData = () => {
  return useQuery({
    queryKey: ['page'],
    queryFn: () => fetchPage(),
  });
};

export { usePageData, fetchPage };
