import { useQuery } from '@tanstack/react-query';

export const helperUrl = async (url: string, page: number | null) => {
  if (
    document.URL.split('/').slice(0, -2).join('/') === window.location.origin
  ) {
    return await fetch(`${window.location.origin}${url}${page ? page : ''}`);
  } else if (document.URL.split('/').slice(0, -2).join('/') === 'https:/') {
    return await fetch(`${window.location.origin}${url}${page ? page : ''}`);
  } else {
    if (document.URL.split('/').length === 7) {
      return await fetch(
        `${document.URL.split('/').slice(0, -3).join('/')}${url}${
          page ? page : ''
        }`
      );
    } else if (document.URL.split('/').length === 6) {
      return await fetch(
        `${document.URL.split('/').slice(0, -2).join('/')}${page ? page : ''}`
      );
    }
  }
};

const fetchPosts = async (page = 1) => {
  let response = await helperUrl(
    '/wp-json/wp/v2/link_in_bio_posts?per_page=10&page=',
    page
  );

  let data = await response?.json();
  return { data: data, totalPages: response?.headers.get('X-WP-TotalPages') };
};

const usePosts = (page: number) => {
  return useQuery({
    queryKey: ['posts', page],
    queryFn: () => fetchPosts(page),
  });
};

export { usePosts, fetchPosts };
