const helperUrlWithPostData = async (url: string, data: any) => {
  if (
    document.URL.split('/').slice(0, -2).join('/') === window.location.origin
  ) {
    return await fetch(`${window.location.origin}${url}`, {
      method: 'POST',
      body: data,
    });
  } else if (document.URL.split('/').slice(0, -2).join('/') === 'https:/') {
    return await fetch(`${window.location.origin}${url}`, {
      method: 'POST',
      body: data,
    });
  } else {
    if (document.URL.split('/').length === 7) {
      return await fetch(
        `${document.URL.split('/').slice(0, -3).join('/')}${url}`,
        {
          method: 'POST',
          body: data,
        }
      );
    } else if (document.URL.split('/').length === 6) {
      return await fetch(
        `${document.URL.split('/').slice(0, -2).join('/')}${url}`,
        {
          method: 'POST',
          body: data,
        }
      );
    }
  }
};

export const sendForm = async (data: any) => {
  const response = await helperUrlWithPostData(
    `/wp-json/contact-form-7/v1/contact-forms/${data.id}/feedback`,
    data.formData
  );

  return response?.json();
};
