import React from 'react';
import cn from 'clsx';

const classes = {
  root: 'text-[#eeeeee] flex gap-[20px] text-[16px] font leading-[19px] uppercase inline-flex items-center justify-center flex-shrink-0 font-semibold rounded outline-none transition duration-300 ease-in-out focus:outline-none focus:shadow',
  normal: 'bg-[var(--second)] text-white border-none hover:bg-primary-2',
  outline:
    'border border-none bg-[#ffffff] hover:shadow-[0_4px_25px_rgba(0,0,0,0.22)] active:shadow-[0_10px_20px_rgba(0,0,0,0.3)] shadow-[0_4px_20px_rgba(0,0,0,0.15)]',
  disabled: 'border border-none text-gray-500 cursor-not-allowed',
  disabledOutline: 'border border-gray-300 text-gray-400 cursor-not-allowed',
  small: `px-3 py-0 h-9 text-sm h-10`,
  medium: `rounded-[43.5px] p-[15px_5px]`,
  big: `rounded-[5px] p-[15.5px_5px]`,
};

export interface ButtonProps {
  className?: string;
  variant?: 'normal' | 'outline';
  size?: 'big' | 'medium' | 'small';
  active?: boolean;
  type?: 'submit' | 'reset' | 'button';
  disabled?: boolean;
  children: React.ReactNode;
  href: string;
}

const LinkButton: React.FC<ButtonProps> = ({
  href,
  children,
  className,
  variant = 'outline',
  size = 'medium',
  active,
  disabled = false,
  ...props
}) => {
  const rootClassName = cn(
    classes.root,
    {
      [classes.normal]: !disabled && variant === 'normal',
      [classes.disabled]: disabled && variant === 'normal',
      [classes.outline]: !disabled && variant === 'outline',
      [classes.disabledOutline]: disabled && variant === 'outline',
      [classes.small]: size === 'small',
      [classes.medium]: size === 'medium',
      [classes.big]: size === 'big',
    },
    className
  );

  return (
    <a href={href} {...props} className={cn(rootClassName, className)}>
      {children}
    </a>
  );
};

export default LinkButton;
